import React, { Component } from 'react';
import ReactHtmlParser from 'react-html-parser';
import Select from 'react-select';
import { Accordion, Offcanvas, OverlayTrigger, Tooltip, Button } from 'react-bootstrap';
import { connect } from 'react-redux';
import { feedsFilter, tagsFilter, ugcStatusFilter, textFilter, postTypeFilter, resetFilter, collectionsFilter, networkFilter } from '../../../../Services/Actions/assetsActions'
import { GET_NETWORK_ICON, GET_NETWORK_NAME } from '../../../../Services/Utils/utils';
import { baseUrl } from '../../../../Assets/AssetsHelper';


class FilterAside extends Component {
    constructor(props) {
        super(props);
    }
    state = {
        isActiveAside: false,
        feedData: [],
        listTags: [],
        tagString: [],
        selectedOption: null
    };
    onLoadFeedList = () => {
        const { filterList } = this.props.feedData;
        this.setState((prevState) => ({ ...prevState, feedData: filterList }));
    }
    componentWillMount() {
        const { tagList } = this.props
        if (tagList && tagList.length > 0) {
            var listTags = []
            tagList.map((item) => {
                listTags.push({ value: item, label: item })
            })
            this.setState({ listTags })


        }
    }
    componentDidMount() {
        var filterAside = document.getElementById("filter_aside");
        if (filterAside) {
            filterAside.addEventListener("click", this.toogleFilter);
        }
        this.onLoadFeedList()
    }
    toogleFilter = () => {
        this.setState({
            isActiveAside: !this.state.isActiveAside,
        })
        setTimeout(() => {
            var offcanvas = document.querySelector(".offcanvas-backdrop");
            if (offcanvas) {
                offcanvas.classList.add('filter-backdrop');
            }
        }, 100);
    };
    onClickToFeed = (feedId, name) => event => this.props.feedsFilter(feedId, name)
    onClickToUgc = (status) => event => this.props.ugcStatusFilter(status)
    onClickToPostType = (type) => event => this.props.postTypeFilter(type)
    onClickToResetFilter = event => this.props.resetFilter()
    onClickToCollection = (id, name) => event => this.props.collectionsFilter(id, name)
    onClickToNetwork = (id, name) => event => this.props.networkFilter(id, name)
    handleChange = (selectedOption) => {
        if (selectedOption && Object.keys(selectedOption).length > 0) this.props.tagsFilter(selectedOption.value)
    }
    componentWillReceiveProps(nextProps) {
        const { filterList } = nextProps.feedData;
        if (filterList != this.state.feedData) this.setState((prevState) => ({ ...prevState, feedData: filterList }));
    }

    render() {
        const { isActiveAside, feedData, listTags } = this.state
        const { filterBody, collections, networks, isCollection } = this.props

        return <Offcanvas show={isActiveAside} placement={'end'} name={'end'} onHide={this.toogleFilter} className="filter_aside">
            <Offcanvas.Body>
                <div className="d-flex align-items-center justify-content-between px-3 px-xxl-4 py-3 border-bottom">
                    <div className="fs-5 fw-medium">Add Filter</div>
                    <Button variant='link' className="text-decoration-none btn-icon btn-active-light btn-active-color-primary p-0 w-30px h-30px border-0" onClick={this.toogleFilter}>
                        <i className="ri-close-line icon-6"></i>
                    </Button>
                </div>
                {!isCollection ? <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header><span className="text-uppercase fs-8 fw-medium">Collections</span></Accordion.Header>
                        <Accordion.Body className="px-5 py-1">
                            {
                                collections && collections.length > 0 ? collections.map((item) => {
                                    return item.name ? <div key={item.id} className="d-flex align-items-start justify-content-between mb-xxl-3 mb-3">
                                        <label className="form-check flex-grow-1 w-100 overflow-hidden">
                                            {filterBody.collection ? <input className="form-check-input" type="checkbox" name={item.id} checked={Object.keys(filterBody.collection).includes(String(item.id))} onClick={this.onClickToCollection(item.id, item.name)} /> : null}
                                            <OverlayTrigger placement="top" overlay={<Tooltip>{item.name}</Tooltip>}>
                                                <span className="form-check-label d-flex fw-medium me-2 w-100 overflow-hidden">
                                                    <div className="symbol symbol-18 d-flex align-items-center justify-content-center flex-shrink-0 bg-light me-1" style={{ borderRadius: 2 }}>
                                                        {item.thumbnails ?
                                                            <img className="object-center-cover" src={item.thumbnails} height={20} width={20} alt={item.name} style={{ borderRadius: 2 }} />
                                                            : <i className="ri-folder-open-line me-0"></i>}
                                                    </div>
                                                    <span className="text-ellipsis">{item.name}</span>
                                                </span>
                                            </OverlayTrigger>
                                        </label>
                                        <span className="badge text-muted border rounded-4 min-w-30px">{item.assets}</span>
                                    </div> : null
                                }) : null
                            }
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion> : null}
                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header><span className="text-uppercase fs-8 fw-medium">Connections</span></Accordion.Header>
                        <Accordion.Body className="px-5 py-1">
                            {
                                feedData && Object.keys(feedData).length > 0 && feedData.feeds.length > 0 ? feedData.feeds.map((item) => {
                                    return item.name ? <div key={item.feedId} className="d-flex align-items-start justify-content-between">
                                        <label className="form-check mb-xxl-3 mb-3 flex-grow-1 w-100 overflow-hidden">
                                            {filterBody.socialFeed ? <input className="form-check-input" type="checkbox" name={item.feedId} checked={Object.keys(filterBody.socialFeed).includes(String(item.feedId))} onClick={this.onClickToFeed(item.feedId, item.name)} /> : null}
                                            <OverlayTrigger placement="top" overlay={<Tooltip>{ReactHtmlParser(item.name)}</Tooltip>}>
                                                <span className="form-check-label d-flex fw-medium me-2 w-100 overflow-hidden">
                                                    <img className="img-fluid me-1" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(item.networkId)}.svg`)} height={18} width={18} alt={GET_NETWORK_NAME(item.networkId)} icon-data={GET_NETWORK_NAME(item.networkId)} />
                                                    <span className="text-ellipsis">{ReactHtmlParser(item.name)}</span>
                                                </span>
                                            </OverlayTrigger>
                                        </label>
                                        <span className="badge text-muted border rounded-4 min-w-30px">{item.count}</span>
                                    </div> : null
                                }) : null
                            }
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header><span className="text-uppercase fs-8 fw-medium">Networks</span></Accordion.Header>
                        <Accordion.Body className="px-5 py-1">
                            {
                                networks && networks.length > 0 ? networks.map((item) => {
                                    return item.name ? <div key={item.id} className="d-flex align-items-start justify-content-between">
                                        <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                            {filterBody.networks ? <input className="form-check-input" type="checkbox" name={item.id} checked={Object.keys(filterBody.networks).includes(String(item.id))} onClick={this.onClickToNetwork(item.id, item.name)} /> : null}
                                            <span className="form-check-label d-flex fw-medium me-2">
                                                <img className="img-fluid me-1" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(item.id)}.svg`)} height={18} width={18} alt={GET_NETWORK_NAME(item.id)} icon-data={GET_NETWORK_NAME(item.id)} />
                                                {item.name}</span>
                                        </label>
                                    </div> : null
                                }) : null
                            }
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header><span className="text-uppercase fs-8 fw-medium">Tags</span></Accordion.Header>
                        <Accordion.Body className="px-5 pt-1 pb-3">
                            <div className="d-flex align-items-start justify-content-between">
                                <Select className='selectpicker w-100'
                                    classNamePrefix="selectpicker"
                                    value={this.state.selectedOption}
                                    onChange={this.handleChange}
                                    options={listTags}
                                />
                            </div>
                            {filterBody.postTags && Object.keys(filterBody.postTags).length > 0 ? <div className="d-flex w-100 flex-wrap w-100 pt-2 ">
                                {
                                    Object.keys(filterBody.postTags).map((tagItem, tagIndex) => <div key={tagItem} className="badge bg-secondary rounded-pill text-gray-800 border me-1 mb-1 px-2 d-flex align-items-center" style={{ top: 0, maxWidth: '100%' }}>
                                        <span className="text-break" style={{ whiteSpace: 'normal' }}>{tagItem}</span>
                                        <button className="btn btn-sm p-0 ms-1" onClick={event => this.props.tagsFilter(tagItem)}>
                                            <i className="ri-close-line icon-7"></i>
                                        </button>
                                    </div>)
                                }
                            </div> : null}
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <Accordion className="border-bottom" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header><span className="text-uppercase fs-8 fw-medium">Post Type</span></Accordion.Header>
                        <Accordion.Body className="px-5 py-1">
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" id="image_type" onClick={this.onClickToPostType("2,4")} checked={Object.keys(filterBody.postType).includes("2,4")} />
                                    <span className="form-check-label fw-medium">Image Only</span>
                                </label>
                            </div>
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" id="video_type" onClick={this.onClickToPostType("3,5")} checked={Object.keys(filterBody.postType).includes("3,5") ? true : false} />
                                    <span className="form-check-label fw-medium">Video Only</span>
                                </label>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <Accordion className="border-bottom d-none" flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header><span className="text-uppercase fs-8 fw-medium">Other</span></Accordion.Header>
                        <Accordion.Body className="px-5 py-1">
                            {/* <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" value="" />
                                    <span className="form-check-label">Highlight Post</span>
                                </label>
                            </div> */}
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" value="" />
                                    <span className="form-check-label fw-medium">Pinned to top</span>
                                </label>
                            </div>
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" value="" />
                                    <span className="form-check-label fw-medium">Recent</span>
                                </label>
                            </div>
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" name="" value="" />
                                    <span className="form-check-label fw-medium">Retweets</span>
                                </label>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <Accordion flush>
                    <Accordion.Item defaultactivekey="0">
                        <Accordion.Header><span className="text-uppercase fs-8 fw-medium">UGC Rights</span></Accordion.Header>
                        <Accordion.Body className="px-5 py-1">
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" checked={Object.keys(filterBody.ugcStatus).includes("1")} onClick={this.onClickToUgc("1")} />
                                    <span className="form-check-label fw-medium">Pending Post</span>
                                </label>
                            </div>
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" checked={Object.keys(filterBody.ugcStatus).includes("2")} onClick={this.onClickToUgc("2")} />
                                    <span className="form-check-label fw-medium">Approved Post</span>
                                </label>
                            </div>
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" checked={Object.keys(filterBody.ugcStatus).includes("3")} onClick={this.onClickToUgc("3")} />
                                    <span className="form-check-label fw-medium">Rejected Post</span>
                                </label>
                            </div>
                            <div className="d-flex align-items-start justify-content-between">
                                <label className="form-check mb-xxl-3 mb-3 flex-grow-1">
                                    <input className="form-check-input" type="checkbox" checked={Object.keys(filterBody.ugcStatus).includes("4")} onClick={this.onClickToUgc("4")} />
                                    <span className="form-check-label fw-medium">Expired Post</span>
                                </label>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <div className="d-flex justify-content-center text-align-center flex-column w-100 px-4 pb-4">
                    {/* <button type="button" className="btn btn-primary w-100 mb-1 d-none">Filter</button> */}
                    <button type="button" className="btn border-0 text-hover-primary btn-sm text-decoration-underline text-muted p-1" onClick={this.onClickToResetFilter}>Reset results</button>
                </div>
            </Offcanvas.Body>
        </Offcanvas>
    }
}

const mapStateToProps = state => {
    return {
        feedData: state.feedData,
        filterBody: state.assets.filterBody,
        networks: state.networks.networkData,
        tagList: state.tags.tagList
    }
}
const mapDispatchToProps = dispatch => {
    return {
        feedsFilter: (refId, name) => dispatch(feedsFilter(refId, name)),
        tagsFilter: (refId) => dispatch(tagsFilter(refId)),
        ugcStatusFilter: (refId) => dispatch(ugcStatusFilter(refId)),
        textFilter: (refId) => dispatch(textFilter(refId)),
        postTypeFilter: (refId) => dispatch(postTypeFilter(refId)),
        resetFilter: () => dispatch(resetFilter()),
        collectionsFilter: (refId, name) => dispatch(collectionsFilter(refId, name)),
        networkFilter: (refId, name) => dispatch(networkFilter(refId, name)),



    }
}
export default connect(mapStateToProps, mapDispatchToProps)(FilterAside);